import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Image } from '../types';
import { 
  Container, Typography, Paper, Stack, Button, 
  FormControl, InputLabel, Select, MenuItem, SelectChangeEvent 
} from '@mui/material';
import Results from '../components/Results';
import { calculateImageAverage } from '../utils/ratingUtils';

// 카테고리 Enum 객체
export const Categories = {
  메밀꽃: '메밀꽃',
  핑크뮬리: '핑크뮬리',
  숲프레임: '숲프레임',
  돌담: '돌담',
  바다: '바다',
} as const;

type Category = keyof typeof Categories | '모두';

const ResultsPage: React.FC = () => {
  const [images, setImages] = useState<Image[]>([]);
  const [numTopImages, setNumTopImages] = useState<number>(10); // 기본값: 상위 10개
  const [selectedCategory, setSelectedCategory] = useState<Category>('모두'); // 기본값: 모두 보기
  const navigate = useNavigate();

  useEffect(() => {
    const storedImages = localStorage.getItem('imageRatings');
    if (storedImages) {
      const parsedImages: Image[] = JSON.parse(storedImages);
      const sortedImages = [...parsedImages]
        .filter((image) => !!image.ratings)
        .sort((a, b) => {
          const avgA = calculateImageAverage(a.ratings || []);
          const avgB = calculateImageAverage(b.ratings || []);
          return avgB - avgA; // 높은 점수 순으로 정렬
        });
      setImages(sortedImages);
    }
  }, []);

  const handleTopImagesChange = (event: SelectChangeEvent<number>) => {
    setNumTopImages(Number(event.target.value));
  };

  const handleCategoryChange = (event: SelectChangeEvent<Category>) => {
    setSelectedCategory(event.target.value as Category);
  };

  const copyFileNamesToClipboard = () => {
    const fileNames = images.map((image) => image.fileName).join('\n');
    navigator.clipboard.writeText(fileNames).then(() => {
      alert('이미지 파일명이 클립보드에 복사되었습니다.');
    }).catch((err) => {
      console.error('클립보드 복사 실패:', err);
    });
  };

  // 카테고리별 필터링된 이미지 목록 계산
  const filteredImages = selectedCategory === '모두'
    ? images
    : images.filter((image) => image.category as unknown as string === selectedCategory);

  if (images.length === 0) {
    return (
      <Container maxWidth="lg" style={{ marginTop: '20px' }}>
        <Typography variant="h5">별점을 정한 사진이 없습니다.</Typography>
        <Button variant="contained" color="primary" onClick={() => navigate('/')}>메인으로</Button>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" style={{ marginTop: '50px', padding: '3px' }}>
      <Paper elevation={3} style={{ padding: '3px', borderRadius: '12px' }}>
        <Typography variant="h4" gutterBottom>점수 결과</Typography>

        <Stack direction="row" spacing={2} marginBottom={2}>
          {/* 사진 개수 선택 */}
          <FormControl fullWidth>
            <InputLabel>사진 개수</InputLabel>
            <Select value={numTopImages} onChange={handleTopImagesChange}>
              {[10, 20, 30, 40, 50, 100].map((count) => (
                <MenuItem key={count} value={count}>상위 {count}개</MenuItem>
              ))}
            </Select>
          </FormControl>

          {/* 카테고리 선택 */}
          <FormControl fullWidth>
            <InputLabel>카테고리</InputLabel>
            <Select value={selectedCategory} onChange={handleCategoryChange}>
              <MenuItem value="모두">모두</MenuItem>
              {Object.keys(Categories).map((category) => (
                <MenuItem key={category} value={category}>{Categories[category as keyof typeof Categories]}</MenuItem>
              ))}
            </Select>
          </FormControl>
        </Stack>

        {/* 결과 표시 */}
        <Results images={filteredImages.slice(0, numTopImages)} />

        <Stack direction="row" spacing={2} justifyContent="center" marginTop={2}>
          <Button variant="contained" color="primary" onClick={() => navigate('/')}>처음으로</Button>
          <Button
            variant="outlined"
            color="secondary"
            onClick={() => {
              localStorage.removeItem('imageRatings');
              navigate('/');
            }}
          >
            초기화
          </Button>
          <Button
            variant="contained"
            color='primary'
            onClick={copyFileNamesToClipboard}
          >
            내보내기
          </Button>
        </Stack>
      </Paper>
    </Container>
  );
};

export default ResultsPage;
