import React, { useState } from 'react';
import { Image } from '../types';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Navigation } from 'swiper/modules';
import { Typography, Card, CardContent, CardMedia, Button, Box } from '@mui/material';
import RotateLeftIcon from '@mui/icons-material/RotateLeft';
import 'swiper/css'; 
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { calculateImageAverage } from '../utils/ratingUtils';

interface ResultsProps {
  images: Image[];
}

// test

const Results: React.FC<ResultsProps> = ({ images }) => {
  const [rotation, setRotation] = useState<number[]>(images.map(() => 0)); // 초기 회전 각도 설정
  const [currentIndex, setCurrentIndex] = useState<number>(0); // 현재 슬라이드 인덱스 (0부터 시작)

  const rotateImage = (index: number) => {
    setRotation((prevRotation) => {
      const newRotation = [...prevRotation];
      newRotation[index] = (newRotation[index] + 90) % 360; // 90도씩 회전
      return newRotation;
    });
  };

  const handleSlideChange = (swiper: any) => {
    setCurrentIndex(swiper.realIndex); // 현재 슬라이드 인덱스 업데이트
  }; 

  return (
    <Box>
      {/* 현재 슬라이드 인덱스 / 전체 사진 수 표시 */}
      {images.length > 0 && (
        <Typography variant="subtitle1" textAlign="center" marginBottom={2}>
          {currentIndex + 1} / {images.length} 번째 사진
        </Typography>
      )}

      <Swiper
        modules={[Pagination, Navigation]}
        spaceBetween={10}
        slidesPerView={1}
        navigation
        pagination={{ clickable: true, renderBullet: () => '' }} // 회색 점 제거
        style={{ width: '100%', margin: '0 auto' }}
        onSlideChange={handleSlideChange} // 슬라이드 변경 이벤트 핸들러
      >
        {images.map((image, index) => (
          <SwiperSlide key={image.id}>
            <Card style={{ margin: '0 auto' }}>
              <Box 
                sx={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  overflow: 'visible', // 회전 시 이미지가 잘리지 않도록
                  position: 'relative', // 버튼을 절대 위치로 설정하기 위해
                  marginBottom: '10px',
                }}
              > 
                <div
                  style={{
                    transform: `rotate(${rotation[index]}deg)`, // 회전 적용
                    transition: 'transform 0.3s ease', // 부드러운 회전 애니메이션
                  }}
                > 
                  <CardMedia
                    component="img"
                    image={image.path}
                    alt={`사진 ${image.id}`}
                    style={{ objectFit: 'contain', width: '100%', height: 'auto' }}
                  />
                </div>
                <Box position="absolute" top={8} right={8}>
                  <Button
                    variant="contained"
                    color="primary"
                    startIcon={<RotateLeftIcon />}
                    onClick={() => rotateImage(index)}
                  >
                    회전
                  </Button>
                </Box>
              </Box>
              <CardContent>
                <Typography variant="subtitle1" textAlign={"center"} gutterBottom>
                  사진: {image.fileName}
                </Typography>
                <Typography variant="h6" textAlign="center">
                  평균 별점: {calculateImageAverage(image.ratings || [])}
                </Typography>
              </CardContent>
            </Card>
          </SwiperSlide>
        ))}
      </Swiper>
    </Box>
  );
};

export default Results;
