import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { Image, ImageRatings } from '../types';

import ImageRating from '../components/ImageRating';
import { Container, Typography, Paper, CircularProgress, Stack, Button } from '@mui/material';
import { loadImagesByCategory } from '../utils/loadImages'; // 카테고리별 이미지를 로드하는 함수
import { calculateImageAverage } from '../utils/ratingUtils';

// 모든 이미지를 로드하는 함수
export const allLoadImages = async () => {
  const response = await fetch('/photo-rating-deploy/imageList.json');
  const images = await response.json();
  return images as ImageRatings; // 모든 이미지 반환
};

const RatingPage: React.FC = () => {
  const [images, setImages] = useState<Image[]>([]); // 선택한 카테고리의 이미지
  const [allImages, setAllImages] = useState<Image[]>([]); // 모든 이미지 상태
  const [currentIndex, setCurrentIndex] = useState(0); // 현재 이미지 인덱스
  const navigate = useNavigate();
  const location = useLocation();
  const { category, minRating } = location.state as { category: string, minRating: number };

  useEffect(() => {
    const fetchImages = async () => {
      const storedRatings = localStorage.getItem('imageRatings');
      let data: Image[] = [];

      if (storedRatings) {
        const ratingsData : Image[] = JSON.parse(storedRatings);
        // 로컬 스토리지에서 해당 카테고리의 이미지 가져오기
        data = ratingsData.filter((data) => data.category as unknown === category && (data.ratings && calculateImageAverage(data.ratings) >= minRating || data.ratings === undefined)) || []; // 카테고리별로 이미지를 가져옴
      }

      if (data.length === 0) {
        // 로컬 스토리지에 데이터가 없으면 서버에서 가져오기
        data = await loadImagesByCategory(category);
      }

      setImages(data);
    };

    const fetchAllImages = () => {
      const storedRatings = localStorage.getItem('imageRatings');
      const allData : Image[] = storedRatings ? JSON.parse(storedRatings) : []; // 로컬 스토리지에서 데이터 가져오기

      if (allData.length === 0) {
        // 로컬 스토리지에 데이터가 없으면 서버에서 가져오기
        allLoadImages().then((data) => {
          setAllImages(data); // 모든 이미지 상태 업데이트
        });
      } else {
        setAllImages(allData.filter((data) =>(data.ratings && calculateImageAverage(data.ratings) >= minRating || data.ratings === undefined))); // 로컬 스토리지에서 데이터 설정
      }
    };

    fetchImages();
    fetchAllImages(); // 모든 이미지 로드
  }, [category]);

  const handleRating = (rating: number) => {
    const updatedImages = [...images];
    const currentImage = updatedImages[currentIndex];

    // 기존 별점 정보가 있는지 확인하고 추가
    const newRatings = [
      ...(currentImage.ratings || []), // 기존 별점 가져오기
      rating, // 새로운 별점 추가
    ]; 

    currentImage.ratings = newRatings; // 새로운 배열로 업데이트

    setImages(updatedImages); // 상태 업데이트

    // 모든 이미지를 로컬 스토리지에 저장
    const updatedAllImages = allImages.map((image) => {
      const updatedImage = updatedImages.find((img) => img.id === image.id);
      return updatedImage ? updatedImage : image; // 업데이트된 이미지가 있으면 대체, 아니면 기존 이미지 유지
    });

    localStorage.setItem('imageRatings', JSON.stringify(updatedAllImages)); // 모든 별점 정보를 저장

    if (currentIndex + 1 < images.length) {
      setCurrentIndex(currentIndex + 1);
    } else {
      navigate('/results');
    }
  };

  if (images.length === 0) return <CircularProgress />;

  return (
    <Container maxWidth="lg" style={{ marginTop: '20px', padding: '3px' }}>
      <Paper elevation={3} style={{ padding: '3px', borderRadius: '12px' }}>
        <Typography variant="h5" gutterBottom>
          사진 {currentIndex + 1} / {images.length}
        </Typography>
        <ImageRating image={images[currentIndex]} onRate={handleRating} />
        <Stack direction="row" spacing={2} justifyContent="center" marginTop={2}>
          <Button variant="outlined" color="secondary" onClick={() => navigate('/')}>
            메인으로
          </Button>
        </Stack>
      </Paper>
    </Container>
  );
};

export default RatingPage;
